"use client";

import { Heading, Text, VStack, SimpleGrid, Box, useColorModeValue } from "@chakra-ui/react";
import ProgramPageLayout from "../../../components/layout/ProgramPageLayout";
import AnimatedSection from "../../../components/layout/AnimatedSection";

export default function TrinityForcePage() {
  return (
    <ProgramPageLayout
      title="Trinity Force"
      tagline="Team 8732 - Developing a New Wave of STEM Leaders"
      logoLight="/images/logos/trinity-force-light-mode.png"
      logoDark="/images/logos/trinity-force-dark-mode.png"
    >
      {/* About Trinity Force */}
      <AnimatedSection id="about" variant="alt">
        <VStack spacing={6} align="start">
          <Heading as="h2" size="xl">
            About Trinity Force
          </Heading>
          <Text fontSize="lg">
            Team 8732 Trinity Force began as an all-girls high-school robotics team from the Greater New Orleans Region competing under the FIRST Robotics Competition banner. Going into the 2024 FIRST Robotics Competition season, we migrated our team to the Dallas-Fort Worth Area to reach a new subset of people and continue to inspire more underrepresented students to pursue STEM.
          </Text>
          <Text fontSize="lg">
            We work to develop the knowledge and confidence of our members through competitive robotics and help prepare them for whatever career path they desire. Trinity Force is our initiative to get female students more involved in the robotics field, providing a technically rigorous and supportive space where young women can collaborate, innovate, and take ownership of their engineering ideas.
          </Text>
        </VStack>
      </AnimatedSection>

      {/* What is FIRST? */}
      <AnimatedSection id="first" variant="default">
        <VStack spacing={6} align="start">
          <Heading as="h2" size="xl">
            What is FIRST?
          </Heading>
          <Text fontSize="lg">
            FIRST (For Inspiration and Recognition of Science and Technology) is a worldwide organization that promotes inspiration and recognition of science and technology. FIRST's mission is to show students of every age the beauty of STEM through problem-solving and innovation, two crucial aspects that have proven paths to a successful career.
          </Text>
          <Text fontSize="lg">
            By inspiring young people to be science and technology leaders and engaging them in mentor-based programs that build upon their STEM knowledge, FIRST fosters well-rounded talents. These prodigies will cultivate life capabilities that include teamwork, communication, and leadership. Additionally, students will establish lifelong connections to aid them in their present or future aspirations.
          </Text>
        </VStack>
      </AnimatedSection>

      {/* Student Role */}
      <AnimatedSection id="student-roles" variant="alt">
        <VStack spacing={8}>
          <Heading as="h2" size="xl" textAlign="center">
            The Student's Role
          </Heading>
          <Text fontSize="lg" textAlign="center" maxW="3xl">
            The students of Trinity Force are what the team was founded on and the whole purpose of FIRST Robotics. Technical skills such as computer programming, CAD, and 3D modeling, along with other skills such as filmmaking and business, are just some of the things that students will get a chance to work through.
          </Text>
          <Text fontSize="lg" textAlign="center" maxW="3xl" fontWeight="semibold">
            All students need to do is come excited and open to learning!
          </Text>

          <SimpleGrid columns={{ base: 1, md: 2, lg: 3 }} spacing={6} w="100%">
            <SkillCard
              title="Mechanical Design"
              description="Learn CAD modeling, fabrication, and hands-on building of robot systems."
            />
            <SkillCard
              title="Programming"
              description="Develop skills in Java, autonomous control, and sensor integration."
            />
            <SkillCard
              title="Strategy & Drive"
              description="Master game analysis, strategy planning, and robot operation during competitions."
            />
            <SkillCard
              title="Business & Media"
              description="Develop marketing materials, manage social media, and build team brand."
            />
            <SkillCard
              title="Project Management"
              description="Coordinate team efforts, manage timelines, and organize events."
            />
            <SkillCard
              title="Leadership"
              description="Build confidence, communication skills, and the ability to inspire others."
            />
          </SimpleGrid>
        </VStack>
      </AnimatedSection>

      {/* Mentor Role */}
      <AnimatedSection id="mentor-roles" variant="default">
        <VStack spacing={6} align="start">
          <Heading as="h2" size="xl">
            The Mentor's Role
          </Heading>
          <Text fontSize="lg">
            The mentors of Trinity Force are responsible for guiding students through the intricate challenges they will face, allowing students to take on more complex topics throughout the season. Helping students become the best version of themselves not only on the team but also as excellent members of society.
          </Text>
          <Text fontSize="lg" fontWeight="semibold">
            The end goal is to have left an impact on their students so great that it pushes them to keep working towards excellence!
          </Text>
        </VStack>
      </AnimatedSection>

      {/* Impact & Achievements */}
      <AnimatedSection id="achievements" variant="alt">
        <VStack spacing={6}>
          <Heading as="h2" size="xl" textAlign="center">
            Our Impact
          </Heading>
          <SimpleGrid columns={{ base: 1, md: 3 }} spacing={8} w="100%">
            <AchievementCard
              stat="World Championship"
              description="Qualified for FIRST Robotics World Championship in our rookie season"
            />
            <AchievementCard
              stat="Rookie Inspiration Award"
              description="Winner of the prestigious Rookie Inspiration Award at the World Championship"
            />
            <AchievementCard
              stat="75% STEM Degrees"
              description="Of graduating seniors have gone on to pursue STEM degrees in college"
            />
          </SimpleGrid>
        </VStack>
      </AnimatedSection>
    </ProgramPageLayout>
  );
}

// Skill Card Component
function SkillCard({ title, description }) {
  const cardBg = useColorModeValue("surface.light", "surface.dark");
  const cardBorder = useColorModeValue("blackAlpha.100", "whiteAlpha.200");
  const headingColor = useColorModeValue("text.light", "text.dark");
  const textColor = useColorModeValue("text.mutedLight", "text.mutedDark");

  return (
    <Box
      p={6}
      bg={cardBg}
      borderRadius="lg"
      borderWidth="1px"
      borderColor={cardBorder}
      shadow="md"
    >
      <VStack align="start" spacing={3}>
        <Heading as="h3" size="md" color={headingColor}>
          {title}
        </Heading>
        <Text color={textColor}>{description}</Text>
      </VStack>
    </Box>
  );
}

// Achievement Card Component
function AchievementCard({ stat, description }) {
  const cardBg = useColorModeValue("surface.light", "surface.dark");
  const cardBorder = useColorModeValue("blackAlpha.100", "whiteAlpha.200");
  const statColor = useColorModeValue("brand.600", "brand.300");
  const textColor = useColorModeValue("text.mutedLight", "text.mutedDark");

  return (
    <VStack
      p={8}
      bg={cardBg}
      borderRadius="lg"
      borderWidth="1px"
      borderColor={cardBorder}
      shadow="md"
      spacing={4}
      textAlign="center"
    >
      <Heading as="h3" size="lg" color={statColor}>
        {stat}
      </Heading>
      <Text color={textColor}>{description}</Text>
    </VStack>
  );
}
